function isMentor() {
	return MENTOR_MODE;
}

function reportErrorToUser(err) {
	vex.dialog.alert(err + '');
}

function getQueryParams(qs) {
	qs = qs.split('+').join(' ');
	var params = {},
	    tokens,
	    re = /[?&]?([^=]+)=([^&]*)/g;
	while (tokens = re.exec(qs)) {
		params[decodeURIComponent(tokens[1])] = decodeURIComponent(tokens[2]);
	}
	return params;
}

function fillText(className, text) {
	var spans = document.getElementsByClassName(className);
	for (var s = 0; s < spans.length; s++) {
		spans[s].innerText = text;
	}
}

function fillSrc(className, text) {
	var spans = document.getElementsByClassName(className);
	for (var s = 0; s < spans.length; s++) {
		spans[s].src = text;
	}
}

function onTabClick(tabGroup, tab) {
	var tabid = tab.dataset.tab;
	var showTab = document.getElementById(tabid);
	if (showTab) {
		database.getPrometheus().save({
			type: 'CHANGE_TAB',
			tid: TEAM_ID,
			tab: tabid
		});
		for (var j = 0; j < tabGroup.length; j++) {
			tabGroup[j].classList.remove('is-active');
		}
		var otherTabs = document.getElementsByClassName('tabbed-container');
		for (var i = 0; i < otherTabs.length; i++) {
			otherTabs[i].style.display = 'none';
		}
		showTab.style.display = 'block';
		tab.classList.add('is-active');
	}
}

var cancels = 0;
function initWithTeamCode(_callback) {
	if (TEAM_ID) {
		_callback(TEAM_ID);
	} else {
		vex.dialog.prompt({
			message: 'Enter your team code:',
			callback: function callback(code) {
				if (code) {
					_callback(code);
				} else if (cancels >= 1) {
					window.location = './';
				} else {
					cancels++;
					initWithTeamCode(_callback);
				}
			}
		});
	}
}

